  import React from "react";
  import { Card, CardBody } from "react-bootstrap";
  import TrueSenseSkeleton from "../../layout/skeleton/trueSenseSkeleton";
  import FilterModeration from "../../layout/common/filterModeration";
  import SectionHeading from "../../layout/common/sectionHeading";
import { kFormatter } from "../../../../../utils";

const TrueSense = (props) => {
  const { truSense, loader, dateFilter, selectedWallFeed } = props;

  return (
    <>
      <Card className="sw-card-custom gutter-b card-shadow mb-5">
        <SectionHeading section={2} icon={'ai'} title={'TruSense Recommendation'} />
        <CardBody className="p-5">
          {loader ? <TrueSenseSkeleton />
            : truSense && Object.keys(truSense).length
              ? truSense.map((data, key) => {
                return (
                  <FilterModeration dateFilter={dateFilter} selectedWallFeed={selectedWallFeed} link={`recommendation=${data.name}`}>
                    <div className="p-6 border rounded">
                      <div className="d-flex align-items-center">
                        <div className="d-flex align-items-start me-2 w-100">
                          <div className="border rounded p-2 me-4">
                            <span className={`bulits ${key == 0 ? 'bg-success' : key == 1 ? 'bg-warning' : 'bg-danger'}  d-block rounded-circle`}></span>
                          </div>
                          <div className="">
                            <p className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name font-size-lg">
                              {data.name}
                            </p>
                            <p className="text-dark mb-0">
                              {data.details}
                            </p>
                          </div>
                        </div>
                        <p className="font-weight-boldest text-dark display-6 m-0">
                          { loader ? 0 :  kFormatter(data.count)}
                        </p>
                      </div>
                    </div>
                  </FilterModeration>
                );
              })
              : null}

        </CardBody>
      </Card>
    </>
  );
};

export default React.memo(TrueSense);
